:root {
  --colorTextos: #49454567;
}
.register {
  background-color: #fed209;
  border-radius: 4px;
  max-width: 1400px;
  height: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

form {
  width: 365px;
  height: 500px;
  background-color: rgba(245, 245, 245, 0.959);

  padding: 25px;
  border-radius: 10px;
  display: inline-block;
}
.form {
  width: 100%;
  margin: auto;
}
.form h1 {
  text-align: center;
  font-weight: 700;
}
form .group {
  position: relative;
  margin: 25px;
}
form .one {
  margin-top: 40px;
}
input {
  background: none;
  color: #c6c6c6;
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 100%;
  border: none;
  border-bottom: 1px solid #49454567;
  border-bottom: 1px solid var(--colorTextos);
}
input:focus {
  outline: none;
  color: #5e5d5d;
}
label {
  color: #49454567;
  color: var(--colorTextos);
  font-size: 16px;
  position: absolute;
  left: 5px;
  top: 10px;
  transition: 0.3s ease-in-out all;
  pointer-events: none;
}
input:focus ~ label,
input:valid ~ label {
  top: -14px;
  font-size: 12px;
  color: black;
}
.barra {
  position: relative;
  display: block;
  width: 100%;
}
.barra::before {
  content: '';
  height: 2px;
  width: 0%;
  bottom: 0;
  position: absolute;
  background: orange;
  transition: 0.3s ease width;
  left: 0;
}
input:focus ~ .barra::before {
  width: 100%;
}

.form button {
  display: block;
  width: 80%;
  margin: 10px auto;
  height: 40px;
  font-size: 16px;
  background-color: orange;
  border-radius: 5px;
  border: none;
  padding: 7px 25px;
  font-weight: 700;
  cursor: pointer;
}

.forgot {
  display: block;
  text-align: center;
  color: rgba(83, 195, 247, 0.8);
  height: 40px;
  font-size: 16px;
  cursor: pointer;
}
.form p {
  text-align: center;
}

.navbar {
  max-width: 1400px;
  height: 70px;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 20px;
  font-weight: bold;
}
.navbar ul {
  width: 100%;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  text-decoration: none;
  -webkit-align-items: center;
          align-items: center;
}

.navbar ul li a {
  text-decoration: none;
  color: black;
}
.navbar ul li a:hover {
  color: orange;
  text-decoration: underline;
}

.profile {
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  -webkit-align-items: center;
          align-items: center;
}
.profile img {
  border-radius: 50%;
  width: 40px;
  margin-left: 10px;
}
.profile__data h3 {
  font-size: 16px;
}
.profile__data p {
  font-size: 12px;
}

